@import url(https://fonts.googleapis.com/css?family=Nanum+Gothic&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{background:#171819;padding:20px}h1{font-size:25px;color:#334476;font-weight:400}.RoundTabs{margin:10px 0px}.RoundTabs ul{border-radius:10px;background:#40434b;width:1200px}.RoundTabs ul li{display:inline-flex;width:33.33%;height:50px;line-height:52px;color:#000000;font-size:20px;font-weight:600;justify-content:center}.Matches{width:1200px;display:flex;justify-content:flex-start;align-content:center;margin-top:100px}.Matches .MathDetail{display:inline-flex;flex-direction:column;align-content:center;width:33.33%}.Matches .MathDetail.one .wapper{position:relative}.Matches .MathDetail.one .wapper hr{position:absolute;background:#4465c5;border:0px;-webkit-transform:translateY(-50%);transform:translateY(-50%)}.Matches .MathDetail.one .wapper .vertical_line{top:50%;content:"";display:block;right:-20px;width:1px;height:105px}.Matches .MathDetail.one .wapper .horizon_line{top:calc(50% - 52px);content:"";display:block;right:-20px;width:40px;height:1px}.Matches .MathDetail.one .wapper .horizon_line.bottom{top:calc(50% + 52px)}.Matches .MathDetail.one .wapper .horizon_line.middle{top:50%;content:"";display:block;right:-40px;width:20px;height:1px}.Matches .MathDetail.two .wapper{position:relative}.Matches .MathDetail.two .wapper hr{position:absolute;background:#4465c5;border:0px;-webkit-transform:translateY(-50%);transform:translateY(-50%)}.Matches .MathDetail.two .wapper .vertical_line{top:50%;content:"";display:block;right:-20px;width:1px;height:105px}.Matches .MathDetail.two .wapper .horizon_line{top:calc(50% - 52px);content:"";display:block;right:-20px;width:40px;height:1px}.Matches .MathDetail.two .wapper .horizon_line.bottom{top:calc(50% + 52px)}.Matches .MathDetail.two .wapper .horizon_line.middle{top:50%;content:"";display:block;right:-40px;width:20px;height:1px}.Matches .MathDetail.two .wapper:first-child .vertical_line{top:calc(50% + 52px)}.Matches .MathDetail.two .wapper:last-child .vertical_line{top:calc(50% - 52px)}.Matches .MathDetail.two .wapper .horizon_line{top:calc(50%)}.Matches .MathDetail.two .wapper .horizon_line.bottom{top:calc(50% + 209px)}.Matches .MathDetail.two .wapper .horizon_line.middle{top:142px}.Matches .MathDetail.two .wapper:last-child .horizon_line{display:none}.Matches .MathDetail.two .wapper{top:8px}.Matches .MathDetail.two .wapper:first-child{top:-8px}.Matches .MathDetail .winner_cup{font-size:0px}.Matches .MathDetail.three{position:relative}.Matches .MathDetail.three .winner_cup{position:absolute;top:calc(50% + 17px);-webkit-transform:translateY(-50%);transform:translateY(-50%);left:310px;display:block;width:57px;height:73px;background:url(data:image/png;base64,) no-repeat;background-size:100%}.Matches .MathDetail.auto_margin>.wapper{margin:auto !important}.Matches .MathDetail>.wapper{margin:auto;margin-bottom:30px}.Matches .MathDetail>.wapper:last-child{margin-bottom:0px}.Matches .MathDetail>.wapper .match{width:330px;display:block;margin:auto}.Matches .MathDetail>.wapper .match:not(:last-child){margin-bottom:30px}.Matches .MathDetail>.wapper .match i{display:inline-block;width:20px;vertical-align:middle;color:#40434b;font-size:18px;font-weight:600;font-style:normal}.Matches .MathDetail>.wapper .match ul{display:inline-block;width:300px;vertical-align:middle}.Matches .MathDetail>.wapper .match ul li{position:relative;width:300px;display:block;height:35px;line-height:35px;border:1px solid #4465c5;background:#2e2f32}.Matches .MathDetail>.wapper .match ul li>span{display:inline-block;vertical-align:middle;color:#ffffff}.Matches .MathDetail>.wapper .match ul li>span.seed{float:left;color:#4465c5;width:30px;text-align:center;font-weight:600}.Matches .MathDetail>.wapper .match ul li>span.name{float:left;text-indent:10px;text-overflow:ellipsis;overflow:hidden;white-space:nowrap;width:200px}.Matches .MathDetail>.wapper .match ul li>span.score{float:right;width:30px;background:#4465c5;text-align:center;font-weight:600}.Matches .MathDetail>.wapper .match ul li>span:after{display:block;content:"";clear:both}.Matches .MathDetail>.wapper .match ul li:first-child{border-radius:10px 10px 0px 0px}.Matches .MathDetail>.wapper .match ul li:first-child .score{border-radius:0px 8px 0px 0px}.Matches .MathDetail>.wapper .match ul li:nth-child(2){border-radius:0px 0px 10px 10px}.Matches .MathDetail>.wapper .match ul li:nth-child(2) .score{border-radius:0px 0px 8px 0px}.Matches .MathDetail>.wapper .match ul li.hovering{background:#4465c5}.Matches .MathDetail>.wapper .match ul li.hovering .seed{color:#2e2f32}.Matches .MathDetail>.wapper .match ul li.winner{z-index:2;border-color:#db6a50}.Matches .MathDetail>.wapper .match ul li.winner .score{background:#db6a50}.Matches .MathDetail>.wapper .match ul li.winner:nth-child(1):after{position:absolute;content:"";display:block;bottom:-1px;width:100%;height:2px;z-index:3;background:#db6a50}.Matches .MathDetail>.wapper .match ul li.winner:nth-child(2):after{position:absolute;content:"";display:block;top:-1px;width:100%;height:2px;z-index:3;background:#db6a50}

